import React, { useContext } from "react";
import Data from "../Data.json";
import { UsernameContext } from "../context";
import MyPost from "./MyPost";
import "./Profile.css";
const PF = process.env.REACT_APP_PUBLIC_FOLDER;

const Profile = () => {
  const me = useContext(UsernameContext);
  const userPosts = Data.filter((post) => post.body === me.username);
  console.log(userPosts);
  return (
    <div className="userProfile">
      <div className="ProfileTitle">
        <img className="ProfileImg" src={PF + me.avatar} alt="" />
        {userPosts.length > 0 && (
          <>
            <h1 className="display-6">My posts</h1>
            {userPosts.map((post) => (
              <MyPost data={post} />
            ))}
          </>
        )}
        {userPosts.length === 0 && (
          <h1 className="display-6">You don't have any posts yet</h1>
        )}
      </div>
    </div>
  );
};

export default Profile;
