import React, { useContext, useState } from "react";
import "./UserSinglePost.css";
import Users from "../Users.json";
import LikesModal from "./LikesModal";
import { UsernameContext } from "../context";

const PF = process.env.REACT_APP_PUBLIC_FOLDER;
const UserSinglePost = (props) => {
  const [modalShow, setModalShow] = useState(false);
  const me = useContext(UsernameContext);
  return (
    <div class="card mx-auto my-2" style={{ width: "20rem" }}>
      <img
        src={PF + props.data.image}
        className="card-img-top"
        alt={props.data.title}
      />
      <div class="card-body expand">
        <h5 class="card-title">{props.data.title}</h5>
        <p class="card-text">
        by:<a href={`/user/${props.data.body}`}> {props.data.body}</a>
        </p>
        <div class="text-muted">
          <div className="likeImgContainer">
            <img className="likeImg one" src={PF + me.avatar} alt="" />
            <img
              className="likeImg two"
              src={
                PF +
                Users.find((user) => user.username === props.data.totalLikes[0])
                  .avatar
              }
              alt=""
            />
            <img
              className="likeImg three"
              src={
                PF +
                Users.find((user) => user.username === props.data.totalLikes[1])
                  .avatar
              }
              alt=""
            />
          </div>
          <div class="likesCounter profile">
            You and{" "}
            <span className="link" onClick={() => setModalShow(true)}>
              {props.data.totalLikes.length} others like this
            </span>{" "}
          </div>
        </div>
        <LikesModal
          key={props.data.totalLikes.length}
          data={props.data.totalLikes}
          me={me}
          show={modalShow}
          onHide={() => setModalShow(false)}
        />
      </div>
    </div>
  );
};

export default UserSinglePost;
